import { marked, Renderer } from 'marked'
// console.log({ Renderer }, new Renderer())
import code from './code.js'
console.log({ code })


const render = new Renderer()
const renderer = {
  heading(text, level) {
    const escapedText = text.toLowerCase().replace(/[^\w]+/g, '-')

    return `
            <h${level}>
              <a name="${escapedText}" class="anchor" href="#${escapedText}">
                <span class="header-link"></span>
              </a>
              ${text}
            </h${level}>`
  },
  code(code, infostring, escaped) {
    // console.log(render.code(code, infostring, escaped))
    return render.code(code, infostring, escaped).replace(/[\u4e00-\u9fa5]/g, (v) => {
      return `<span class="font-zh">${v}</span>`
    })

    // const html = Prism.highlight(code, Prism.languages.javascript, 'javascript');

  }
}

marked.use({ renderer })

export default (arg) => {
  // console.log({ arg })
  if (arg == '') return 'Not Found'
  return marked(arg)
}
