import Snabbdom from 'snabbdom-pragma'
import { rect } from './rect.jsx'
import { multiText } from './text.jsx'

export const normalVNode =
  ({ txt, x, y, width, height, fontcolor, fontsize, stroke, rx = 4, ry = 4, bg = '#fff' }) =>
  (sink) => {
    const textSource = multiText(txt, { width, height, color: fontcolor, size: fontsize })

    return (
      <g transform={`translate(${x},${y})`}>
        {rect({ x: 0, y: 8, width, height: height - 12, fill: bg, rx: rx / 2, ry: ry / 2, stroke })}
        {textSource(0, sink)}
      </g>
    )
  }
