export const color = (color) => (color.indexOf('#') != -1 ? color : `#${color}`)
export const innerVal = (val, factor) => val - padding(val, factor)
export const padding = (val, factor = 20) => {
  const ret = val / factor
  return ret > 8 ? 8 : ret
}

export const strokeAttr = ({ width, type, color }) => {
  // 
  const attr = { color, width }
  const dasharray = {
    solid: false,
    dashed: [4, 4].join(' ')
  }
  attr.dasharray = dasharray[type]
  // console.log({ attr, type })
  return attr
}

export const multiScale = (d, ratios) => {
  const p = createPadding(d)
  return ratios.map((ratio) => p.scale(ratio))
}

export const createPadding = (min) => {
  const scale = (ratio) => {
    let dv = min * ratio
    // if (dv > 10) dv = 5
    return { dv, dx: dv / 2, dy: dv / 2 }
  }
  const limit = (dv) => {
    const absdv = Math.abs(dv)
    if (absdv > 8) dv = (8 * dv) / absdv
    if (dv != 0 && absdv < 4) dv = (4 * dv) / absdv
    return dv
  }
  return {
    scale(ratio) {
      let { dv, dx, dy } = scale(ratio)
      dv = limit(dv)
      return { d: min - dv, dx, dy, dv, padding: dv / 2 }
    }
  }
}

const icon = (g, d, arg) => {
  const p = g.path(d(arg))
  let cache = {}
  return {
    update({ d: arg, fill, isarticle, color }) {
      const options = { fill: isarticle ? color : 'transparent' }
      // const options = { fill: '#444' }
      if (arg) options.d = d(arg)
      if (fill) options.fill = fill
      // console.log({ options })
      p.attr((cache = Object.assign(cache, options)))
      return this
    }
  }
}

export const editIconPath = ({ x, y }) => {
  return `M${x} ${y}h4 l8 -10 v4 l-7 6 h8 v0.6 L${x} ${y + 0.6}z`
}
export const moreIconPath = ({ x, y }) => {
  return `M${x} ${y}h9 v-1 h1 v-10z`
}
export const moreIconCirclePath = ({ x, y, r, tx }) => {
  return `M${x} ${y} A ${r} ${r} 0 0 0 ${tx + x} ${y}`
}




export const editicon = (g, { x: defx, y: defy }) => {
  const d = ({ x = defx, y = defy }) => `M${x} ${y}h4 l8 -10 v4 l-7 6 h8 v0.6 L${x} ${y + 0.6}z`
  return icon(g, d, { x: defx, y: defy })
}
export const moreicon = (g, { x, y }) => {
  const d = ({ x, y }) => `M${x} ${y}h9 v-1 h1 v-10z`
  return icon(g, d, { x, y })
}
export const moreiconOfCircle = (g, { x, y, r, tx }) => {
  const d = ({ x, y, r, tx }) => `M${x} ${y} A ${r} ${r} 0 0 0 ${tx + x} ${y}`
  return icon(g, d, { x, y, r, tx })
}
