import { isSpace, isCtrl, isShift } from './config.js'

export let isSpaceKeyDown = false
export let isShiftKeyDown = false
export let isCtrlKeyDown = false

window.addEventListener(
  'keydown',
  (evt) => {
    if (isSpace(evt)) isSpaceKeyDown = true
    if (isShift(evt)) isShiftKeyDown = true
    if (isCtrl(evt)) isCtrlKeyDown = true
  },
  true
)
window.addEventListener(
  'keyup',
  (evt) => {
    if (isSpace(evt)) isSpaceKeyDown = false
    if (isShift(evt)) isShiftKeyDown = false
    if (isCtrl(evt)) isCtrlKeyDown = false
  },
  true
)
