import { isUnDef } from '../lantu/helpers/base.js'

export const elmt_byid = (id) => document.getElementById(id)
export const elmt_style = (dom, v) => {
  const deserialize = (str) =>
    str
      .split(';')
      .filter((v) => v)
      .map((item) => item.split(':'))
      .reduce((memo, [k, v]) => {
        memo[k] = v
        return memo
      }, {})
  const mergeVal = { ...deserialize(dom.getAttribute('style') || ''), ...deserialize(v) }
  const serialize = (val) =>
    Object.keys(val)
      .reduce((memo, k) => {
        memo.push(`${k}:${val[k]}`)
        return memo
      }, [])
      .join(';')
  dom.setAttribute('style', serialize(mergeVal))
}
export const elmt_display = (dom, v) => elmt_style(dom, `display:${v}`)
export const elmt_classname = (dom, v) => dom.setAttribute('class', v)

// textarea tab 
export const textareaTab = (elm) => {
  var start = elm.selectionStart
  var end = elm.selectionEnd

  elm.value = elm.value.substring(0, start) + '\t' + elm.value.substring(end)
  elm.selectionStart = elm.selectionEnd = start + 1
}

export const showGen = (dom) => {
  let isshow = false
  return {
    show(bool) {
      if (isUnDef(bool) || bool == isshow) return isshow
      elmt_display(dom, (isshow = bool) ? 'block' : 'none')
      return isshow
    }
  }
}
