import { mock } from './model.js'
import { logmid, undef } from '../helpers/base.js'

const LOCAL_DATA = 'DATA'
export const localremove = () => localStorage.removeItem(LOCAL_DATA)
export const localset = (chardata) => localStorage.setItem(LOCAL_DATA, chardata)
export const localget = () => localStorage.getItem(LOCAL_DATA)

// console.log({ mock })
export const createLocal = () => {
  let localStage = {}
  try {
    localStage = undef(localStage, JSON.parse(localget()))
    logmid('STAGE_LIFE::', { localStage })

    // localStage.data.points = mock
    // localStage.data.view.x = -235
    // localStage.data.view.y = 235
    // console.log({ localStage })
  } catch (err) {
    console.warn(err)
  }
  return {
    get: localget,
    set: localset,
    remove: localremove,
    stage: () => localStage,
    clear: () => (localStage = null)
  }
}
