import { EDIT_VIEW_ISSHOW } from '../../plugins/edit/index.js'

export * from './config.js'
export * from './global.js'
// import { isSpaceKeyDown, isShiftKeyDown, isCtrlKeyDown } from './global.js'
import { isEnter, isEditViewKey, isToggleEdit, isLeft, isRight, isTop, isBottom, isZoomIn, isZoomOut, isTagSearch, isViewCenter } from './config.js'

// export const isEditView = (...arg) => isCtrlKeyDown && isEditViewKey(...arg)
export const isEditView = (evt) => isEditViewKey(evt)
export const isCommandEnter = (evt) => !EDIT_VIEW_ISSHOW && isEnter(evt)

export const isToggleCommandEdit = (evt) => !EDIT_VIEW_ISSHOW && isToggleEdit(evt)

export const isGlobalKey = ({ keyCode, ctrlKey, shiftKey }) => isZoomIn({ keyCode, ctrlKey, shiftKey }) || isZoomOut({ keyCode, ctrlKey, shiftKey })

export const isGlobalStageKey = ({ keyCode, ctrlKey, shiftKey }) =>
  isZoomIn({ keyCode, ctrlKey }) ||
  isZoomOut({ keyCode, ctrlKey }) ||
  isLeft({ keyCode }) ||
  isRight({ keyCode }) ||
  isTop({ keyCode }) ||
  isBottom({ keyCode }) ||
  isViewCenter({ keyCode }) ||
  isTagSearch({ keyCode, ctrlKey, shiftKey })

