import { isStr, logmid } from './base.js'
import fallback from 'json-parse-even-better-errors'

export const encodeBr = (v) => logmid('COMMAND::encodeBr:', v) && v.replaceAll('\\n', '\\\n').replaceAll('\n', '\\n')
export const decodeBr = (v) => logmid('COMMAND::decodeBr:', v) && v.replaceAll('\\n', '\n').replaceAll('\\\n', '\\n')
function jsonEscape(str) {
  return str.replace(/\n/g, '\\\\n').replace(/\r/g, '\\\\r').replace(/\t/g, '\\\\t')
}
const replace = (jsonstr) =>
  jsonstr
    .replaceAll('"', '\\"')
    .replaceAll('"', '\\"')
    .replaceAll('\\"', '"')
    .replaceAll('\\"', '"')
    .replaceAll('\\\\"', '\\"')
    .replaceAll('\\\\"', '\\"')
    .replaceAll('\\\\\\"', '\\\\"')
    .replaceAll('\\\\\\"', '\\\\"')
    .replace(/\\+\n/g, '\\n') // \
    .replace(/\\+\\n/g, '\\n') // \
    .replace(/\\+\t/g, '\\t') // \
    .replace(/\\+\\t/g, '\\t') // \
    .replace(/\t/g, '')
// .replace(/\n/g, '')
// .replaceAll('\\\n', '\\n')
// .replaceAll('\\\\n', '\\n')
// .replaceAll('\\\\\n', '\\n')

export const jsonparse = (jsonstr) => {
  // console.log("JSON_PARSE::", fallback(replace(jsonstr)))
  const v = JSON.parse(replace(jsonstr))
  return isStr(v) ? jsonparse(v) : v
}
