function scan(reducer, seed) {
  let hasAcc = arguments.length === 2
  return (source) => (start, sink) => {
    if (start !== 0) return
    let acc = seed
    source(0, (t, d) => {
      if (t === 1) {
        acc = hasAcc ? reducer(acc, d) : ((hasAcc = true), d)
        sink(1, acc)
      } else sink(t, d)
    })
  }
}
