import Snabbdom from 'snabbdom-pragma'

export const endVNode = ({ x, y, d, bg: fill = '#000' }) => {
  const innerR = 6
  const circle = ({ cx = 0, cy = 0, r, fill, opacity }) => {
    return <circle cx={cx} cy={cy} r={r} fill={fill} opacity={opacity}></circle>
  }

  return (
    <g transform={`translate(${x},${y})`}>
      {circle({ r: d / 2, fill, opacity: 0.3 })}
      {circle({ r: innerR / 2, fill, opacity: 1 })}
    </g>
  )
}
