import { mean, min, max } from 'lodash-es'
import { isDef, round } from './base.js'
import { boxcxy, boxtrbl, iscircle, isrect, isgroup } from '../vnode/vnode.js'

const hascircle = (type) => iscircle(type)
const hasrect = (type) => isrect(type) || isgroup(type)
const PI = Math.PI / 2.8
export const r = (d) => d / 2
export const cos = (r) => Math.cos(PI) * r
export const sin = (r) => Math.sin(PI) * r

export const scaled = (v, ratio) => {
  const nv = v * ratio
  return [nv, v - nv]
}

// 
export const overlap = (
  { left: l1, right: r1, top: t1, bottom: b1, width: w1, height: h1 },
  { left: l2, right: r2, top: t2, bottom: b2, width: w2, height: h2 }
) => {
  const sumw = l1 <= l2 ? r2 - l1 : r1 - l2 // , 
  const sumh = t1 <= t2 ? b2 - t1 : b1 - t2

  if (sumw < w1 + w2) return true
  if (sumh < h1 + h2) return true
}

export const trbl = (x, y, width, height) => ({ left: x, right: x + width, top: y, bottom: y + height })
export const overlapPoint = ({ x: x1, y: y1, width: w1, height: h1 }, { x: x2, y: y2, width: w2, height: h2 }) => {
  return overlap(trbl(x1, y1, w1, h1), trbl(x2, y2, w2, h2))
}

// 
const distance = (x1, y1, x2, y2) => Math.pow(x1 - x2, 2) + Math.pow(y1 - y2, 2)
// 
const compare = (points1, points2) => (distance(...points1) < distance(...points2) ? points1 : points2)

// 
const rectcoord = ({ tx, ty }, { top, right, bottom, left }) => {
  const cx = left + (right - left) / 2,
    cy = top + (bottom - top) / 2
  const x2 = tx > cx ? right : left // 
  const y2 = ty > cy ? bottom : top // 
  return compare([tx, ty, x2, cy], [tx, ty, cx, y2])
}

const circlecoord = ({ r, tx, ty }, { x, y }) => {
  const a = x - tx // a 
  const b = y - ty // b 
  const c = Math.sqrt(Math.pow(a, 2) + Math.pow(b, 2)) // c 
  const dx = round((r * a) / c)
  const dy = round((r * b) / c)
  return { dx, dy }
}

// 
export const linecoord = (head, tail) => {
  let { cx: x1, cy: y1 } = boxcxy(head)
  let { cx: x2, cy: y2 } = boxcxy(tail)
  // console.log({ x1, y1, x2, y2 })
  if (hasrect(head.type)) {
    //  
    const [_x, _y, newX1, newY1] = rectcoord({ tx: x2, ty: y2 }, boxtrbl(head))
    ;(x1 = newX1), (y1 = newY1)
  }

  if (hasrect(tail.type)) {
    // 
    // const { cx, cy } = boxcxy(head)
    const [_x, _y, newX2, newY2] = rectcoord({ tx: x1, ty: y1 }, boxtrbl(tail))
    ;(x2 = newX2), (y2 = newY2)
  }

  if (hascircle(head.type)) {
    const r = head.d / 2
    const { dx, dy } = circlecoord({ r, tx: x1, ty: y1 }, { x: x2, y: y2 })
    x1 += dx
    y1 += dy
  }
  // 
  if (hascircle(tail.type)) {
    // 
    const r = tail.d / 2
    const { dx, dy } = circlecoord({ r, tx: x2, ty: y2 }, { x: x1, y: y1 })
    x2 += dx
    y2 += dy
  }
  // console.log({ x1, y1, x2, y2 })

  return { x1, y1, x2, y2 }
}
export const linecoordArr = (head, tail) => {
  const { x1, y1, x2, y2 } = linecoord(head, tail)
  return [
    { x: x1, y: y1 },
    { x: x2, y: y2 }
  ]
}

// 
export const boundary = (vnodes) => {
  let [left, top, right, bottom] = [0, 0, 0, 0]
  vnodes.forEach(({ x, y }, i) => {
    if (i == 0) {
      left = right = x
      top = bottom = y
    } else {
      if (x < left) left = x
      if (x > right) right = x
      if (y < bottom) top = y
      if (y > bottom) bottom = y
    }
  })
  return { left, top, right, bottom }
}

//
export const transformXY = (translate) => {
  if (!translate) return { x: 0, y: 0 }
  const [_, val] = translate.match(/.+\((.+)\)/)
  const [x, y] = val.split(',')
  return { x: +x, y: +y }
}

// 
export const dev = (d1, d2) => d1 - d2 < 10

// 
export const avginc = (arr) => {
  // 
  const len = arr.length
  if (len <= 2) return

  const minval = min(arr)
  const maxval = max(arr)
  const stepval = (maxval - minval) / (len - 1)
  const inc = (_, i) => minval + stepval * (i + 1)

  return [
    minval,
    ...Array(len - 2)
      .fill(0)
      .map(inc),
    maxval
  ]
}
