import { ARTICLE } from './resource/config.js'

import { logmap, isUnDef, undef, nowsec } from '../helpers/base.js'

import { cacheUid } from './cache.js'
export const createArticle = (db) => {
  const cuid = cacheUid(db, ARTICLE)()

  return {
    minuid: cuid.minuid,
    maxuid: cuid.maxuid,
    allUids() {
      return this.fetch().then((list) => list.filter(({ data }) => !!data.trim()).map(({ uid }) => uid))
    },
    fetch(uid) {
      return db.fetch(ARTICLE, uid)
    },
    fetchUid(uid, ismapping) {
      if (isUnDef(uid)) return Promise.resolve([])
      // return db.select(ARTICLE, { uid }, { ismapping })
      return this.fetch(uid)
    },
    fetchIn: (uids) => db.select(ARTICLE, { uid: { in: uids } }),
    delete(uid) {
      console.log({ uid })
      const ret = db.removeUid(ARTICLE, uid)
      cuid.modify([uid])
      return ret
    },
    async send({ uid, data, updateTime = nowsec(), syncTime = 0 }, ismapping) {
      const article_id = undef(0, uid)
      let isupdate = false,
        isinsert = false

      const article = await this.fetchUid(uid, ismapping)

      if (article.length > 0) {
        await db.update(ARTICLE, { set: { data, updateTime, syncTime }, where: { uid } })
        isupdate = true
      } else {
        if (db.table(ARTICLE).lock()) return
        if (isUnDef(uid)) {
          uid = cuid.max(cuid.max() + 1)
        } else if (uid > cuid.max()) {
          uid = cuid.max(uid)
        } else {
          console.error('uid   maxuid::', { uid }, cuid.max())
        }
        // uid = cuid.max() + 1
        // await db.insert(ARTICLE, [].concat({ uid, updateTime, syncTime, data }), { dbuid: article_id })
        await db.insert(ARTICLE, [].concat({ uid, updateTime, syncTime, data }))
        // cuid.max(uid)
        isinsert = true
      }
      return { uid, isinsert, isupdate }
    }
  }
}
