import Snabbdom from 'snabbdom-pragma'
import { r, sin, cos } from '../../helpers/coord.js'
import { multiScale, strokeAttr, padding, innerVal, color, editIconPath, moreIconPath, moreIconCirclePath } from '../node.js'
import { multiText } from './text.jsx'

export const circleVNode =
  ({ x, y, d, txt, fontsize, fontcolor, bg = '#fff', stroke, isarticle, isstage, ismore = false }) =>
  (sink) => {
    const ratios = [0, -2 / 20]
    const [inner, wrap] = multiScale(d, ratios)

    const defr = r(d) //  cx,cy 
    const editiconArg = (s) => ({ x: defr - r(12), y: -r(s.d) + defr + 12 })
    const moreiconArg = (d, s) => ({
      x: r(d) - cos(r(s.d)), // 
      y: r(d) + sin(r(s.d)), // 
      tx: cos(r(s.d)) * 2, // *2, 
      r: r(s.d) // 
    })
    // stroke.dasharray = (stroke.dasharray || []).join(' ')
    // if (!stroke.dasharray) {
    //   stroke.dasharray = false
    // }
    const textSource = multiText(txt, { width: d, height: d, color: fontcolor, size: fontsize })
    const stageFlag = (isstage) => {
      if (!isstage) return ''
      return circle({
        cx: wrap.padding + wrap.d / 2,
        cy: wrap.padding + wrap.d / 2,
        r: wrap.d / 2,
        fill: 'transparent',
        stroke: { ...stroke, width: 1, opacity: 0.25 }
      })
    }
    const articleFlag = (isarticle) => {
      if (!isarticle) return ''
      return <path d={editIconPath(editiconArg(inner))} fill="#000000"></path>
    }
    const moreFlag = (ismore) => {
      if (!ismore) return ''
      return <path d={moreIconCirclePath(moreiconArg(d, inner))} fill="#000000"></path>
    }

    console.log(inner.d)

    return (
      <g transform={`translate(${x},${y})`}>
        {stageFlag(isstage)}
        {circle({
          cx: inner.padding + inner.d / 2,
          cy: inner.padding + inner.d / 2,
          r: inner.d / 2,
          fill: bg,
          stroke
        })}
        {textSource(0, sink)}
        {articleFlag(isarticle)}
        {moreFlag(ismore)}
      </g>
    )
  }
export const circle = ({ cx, cy, r, fill, stroke, className = false }) => {
  const { width: strokewidth = false, dasharray = false, opacity: strokeopacity = false, color = false } = stroke || {}
  return (
    <circle
      className={className}
      cx={cx}
      cy={cy}
      r={r}
      fill={fill}
      stroke-width={strokewidth}
      stroke-dasharray={dasharray}
      stroke-opacity={strokeopacity}
      stroke={color}
    ></circle>
  )
}
