import { OPERATION, ARTICLE, STAGE } from './resource/config.js'
export * from './resource/config.js'

import { createDB } from './resource/client.js'
import { createArticle } from './article.js'
import { createNode } from './node.js'
import { createStage } from './stage.js'
import { createOperation } from './operation.js'
import { createMapping } from './mapping.js'
import { middleware } from './middleware.js'
import { isUnDef } from '../helpers/base.js'

export const db = createDB({ middleware })
export const db_node = createNode(db)
export const db_article = createArticle(db)
export const db_stage = createStage(db)
export const db_operation = createOperation(db)
// export const db_mapping = createMapping(db)

export const db_service = {
  fetchOpWithMap() {
    return Promise.all([db_mapping.fetch(), db_operation.fetch()]).then(([mappings, operations]) => {
      console.log({ mappings, operations })
      return operations.map((operation) => {
        const { entity, entityUid } = operation
        const entityDBUid = db_mapping.map.ltr(entity, entityUid)
        if (!isUnDef(entityDBUid)) {
          operation.entityUid = entityDBUid
        }
        return operation
      })
    })
  }
}
