import Snabbdom, { has } from 'snabbdom-pragma'
import { rect } from './rect.jsx'
import { circle } from './circle.jsx'
import { text } from './text.jsx'

export const tagVNode = ({ txt, width, height, fontsize, fontcolor, transform, board }) => {
  return (
    <g transform={transform}>
      {board}
      {text({ txt, fontsize, x: width / 2, y: height, fill: fontcolor })}
    </g>
  )
}
export const tagDefVNode = ({ txt, x, y, width, height, bg: fill = '#000', fontsize, fontcolor, className, scale, stroke }) => {
  return tagVNode({
    txt,
    width,
    height: fontsize,
    fontsize,
    fontcolor,
    transform: `translate(${x - width * scale},${y - (height * scale) / 2}) scale(${scale},${scale})`,
    board: rect({ x: 0, y: 0, fill, width, height, rx: 4, ry: 4, stroke, className })
  })
}

export const tagNormalVNode = ({ txt, x, y, width, height, bg: fill = '#000', fontsize, fontcolor, className, scale, stroke }) => {
  return tagDefVNode({ txt, x, y, width, height, bg: fill, fontsize: fontsize + 4, fontcolor, className, scale, stroke })
}

export const tagGroupVNode = ({ txt, x, y, width, height, fontsize, scale, stroke }) => {
  scale = scale * 1.6
  stroke.dasharray = '2, 4'
  // console.log({ stroke })
  return tagDefVNode({ txt, x, y, width, height, bg: '#fff8', fontsize, fontcolor: '#777', scale, stroke })
}
export const tagPointVNode = ({ txt, x, y, width, bg: fill = '#000', fontsize, fontcolor, className, scale }) => {
  scale = scale / 1.5
  return tagVNode({
    txt,
    width: 0,
    height: fontsize / 3,
    fontsize: fontsize,
    fontcolor,
    transform: `translate(${x},${y}) scale(${scale},${scale})`,
    board: circle({ cx: 0, cy: 0, fill, r: width / 2, className })
  })
}
