import Snabbdom from 'snabbdom-pragma'
import { round } from '../../helpers/base.js'

export const ty = (size, height) => {
  let d = (height * height) / 2000
  d = d > size / 8 && size / 8
  return size / 2 - d
  // return size / 2 - size/8
}

const createAlign = (options) => {
  const is = (reg) => (txt) => txt.match(reg)
  const clear = (reg) => (txt) => txt.replace(reg, '')
  return Object.keys(options).reduce((memo, key) => {
    const reg = options[key]
    memo[key] = {
      is: is(reg),
      clear: clear(reg)
    }
    return memo
  }, {})
}

const aligns = createAlign({
  left: /^.*@left.*\n/,
  right: /^.*@right.*\n/,
  top: /^.*@top.*\n/,
  bottom: /^.*@bottom.*\n/,
  start: /^.*@start.*\n/,
  middle: /^.*@middle.*\n/,
  end: /^.*@end.*\n/,
  min: /^.*@min.*\n/,
  max: /^.*@max.*\n/
})
const alignMiddleware = (val) => {
  // console.log({ val })
  const matchs = Object.keys(aligns).filter((key) => aligns[key].is(val))
  // console.log({ matchs })
  return matchs.reduce(
    (memo, key) => {
      memo.val = aligns[key].clear(val)
      console.log(memo.val)
      memo.aligns[key] = true
      return memo
    },
    { val, aligns: { left: false, right: false, top: false, right: false, start: false, end: false, min: false, max: false } }
  ) //  val
}

const lineHeight = (size) => size + 8
// 
const position = ({ aligns, width, height, size, txts }) => {
  // ,  || 
  let top = (height - lineHeight(size) * (txts.length - 1)) / 2
  let left = +width / 2
  // console.log({ aligns })
  let ratio = 1 / 20
  if (aligns.min) ratio = 1 / 40
  if (aligns.max) ratio = 1 / 10
  if (aligns.left) left = round(width * ratio)
  if (aligns.right) left = width - round(width * ratio)
  if (aligns.top) top = lineHeight(size) / 2
  if (aligns.bottom) top = height - lineHeight(size) * txts.length
  // 
  if (aligns.left && !aligns.middle) aligns.start = true
  if (aligns.right && !aligns.middle) aligns.end = true

  return { top, left, anchor: (aligns.start && 'start') || (aligns.end && 'end') || 'middle' }
}

export const multiText =
  (txt, { width, height, color = '#000', size = 16 }) =>
  (start, sink) => {
    if (txt.trim() == '') return ''
    if (start !== 0) return ''

    const { aligns, val } = alignMiddleware(txt)
    const txts = val.split('\n')
    let { left, top, anchor } = position({ aligns, width, height, size, txts })
    // console.log({ val })

    const tspans = txts.map((t, i) => {
      let dy = lineHeight(size)
      let x = 0
      if (i == 0) dy = top
      if (t == '') {
        t = ' '
        dy = size
      }
      if (t.indexOf('\t') != -1) {
        x = 16
      }
      return (
        <tspan x={x} dy={dy}>
          {t}
        </tspan>
      )
    })

    const setbox = (vnode) => {
      sink(
        new Promise((resolve) => {
          setTimeout(() => {
            // console.log('-----insert----', { vnode }, vnode.elm.getBBox())
            const { width, height } = vnode.elm.getBBox()
            resolve({ width, height })
          })
        })
      )
    }
    const insert = (vnode) => setbox(vnode)
    const update = (ovnode, vnode) => {
      // 
      const txts = (children = []) => children.map(({ text }) => text).join()
      if (txts(ovnode.children) == txts(vnode.children)) return
      // console.log('------------updte ----------')
      setbox(vnode)
    }
    console.log({ height })

    return (
      <text
        hook={{ insert, update }}
        font-size={size}
        text-anchor={anchor}
        font-family="none"
        fill={color}
        transform={`translate(${left}, ${ty(size, height)})`}
      >
        {tspans}
      </text>
    )
  }

export const text = ({ x, y, fontsize, txt, fill }) => {
  return (
    <text font-size={fontsize} text-anchor="middle" font-family="sans-serif" fill={fill} transform={`translate(${x}, ${y})`}>
      {txt}
    </text>
  )
}
