export const islink = (type) => has(['link', 'solid', 'dotted'], type)

export const isdot = (type) => type == 'dot'
export const isgroup = (type) => type == 'group'
export const iscircle = (type) => type == 'circle'
export const isrect = (type) => type == 'rect'
export const isend = (type) => type == 'end'
export const cidparse = (cids) => cids.split(',').map(Number)
export const cidUnParse = (cids) => cids.join(',')

export const unparseDot = (dots) => dots.join('-')
export const dotids = (dots) => dots.map(pickv('id'))
export const dotidstr = (dots) => unparseDot(dotids(dots))
export const parseDotOp = (op) => (dotstr) => dotstr.split('-').map((id) => op(+id))
export const parseDot = (dotstr) => parseDotOp((v) => v)(dotstr)

export const box = ({ id, x, y, width, height, d, _width, _height, _d }) => ({
  id,
  x,
  y,
  width: d || width,
  height: d || height,
  _width: _d || _width,
  _height: _d || _height
})
// 
export const boxcxy = (vnode) => {
  const vbox = box(vnode)
  // console.log({ vbox })
  return { ...vbox, cx: vbox.x + vbox.width / 2, cy: vbox.y + vbox.height / 2 }
}
export const boxtrbl = (vnode) => {
  const vbox = box(vnode)
  let top = vbox.y,
    right = vbox.x + vbox.width,
    bottom = vbox.y + vbox.height,
    left = vbox.x
  if (isend(vnode.type) || isdot(vnode.type)) {
    left = left - END_D / 2
    right = right + END_D / 2
    top = top - END_D / 2
    bottom = bottom + END_D / 2
  }
  return { ...vbox, top, right, bottom, left }
}
export const boxasc = ({ width: w1, height: h1 }, { width: w2, height: h2 }) => w1 * h1 - w2 * h2
export const boxdesc = ({ width: w1, height: h1 }, { width: w2, height: h2 }) => w2 * h2 - w1 * h1

