import { DATA_TYPE } from 'jsstore/dist/jsstore.commonjs2.js'
const DB_TPL = (id) => {
  const common = {
    updateTime: { notNull: true, dataType: DATA_TYPE.number },
    syncTime: { notNull: true, dataType: DATA_TYPE.number }
  }

  const TPL_OPERATION = {
    name: `OPERATION`,
    columns: {
      uid: { primaryKey: true, unique: true },
      entity: { notNull: true, dataType: DATA_TYPE.number }, //  0:stage, 1:article
      entityUid: { notNull: true, dataType: DATA_TYPE.number }, // ID, :stage_id
      type: { notNull: true, dataType: DATA_TYPE.number }, //  0:del,   1:add,     2:update
      ...common
    }
  }
  const TPL_NODE = {
    name: `NODE`,
    columns: {
      uid: { primaryKey: true },
      data: { notNull: true },
      ...common
    }
  }
  const TPL_ARTICLE = {
    name: `ARTICLE`,
    columns: {
      uid: { primaryKey: true, unique: true },
      data: { notNull: true },
      ...common
    }
  }
  const TPL_STAGE = {
    name: `STAGE`,
    columns: {
      uid: { primaryKey: true, unique: true },
      title: { notNull: true },
      parent: { notNull: true, dataType: DATA_TYPE.number }, // parent id
      data: { notNull: true }, // {points,view}
      ...common
    }
  }
  const TPL_REMOTE_MAPPING = {
    name: `REMOTE_MAPPING`,
    columns: {
      entity: { notNull: true, dataType: DATA_TYPE.number }, //  0:stage, 1:article
      entityUid: { notNull: true, dataType: DATA_TYPE.number }, // uid
      entityDBUid: { notNull: true, dataType: DATA_TYPE.number } // uid
    }
  }
  const DB = {
    name: `DATA_${id}`,
    tables: [TPL_OPERATION, TPL_NODE, TPL_ARTICLE, TPL_STAGE, TPL_REMOTE_MAPPING]
  }
  return {
    DB,
    OPERATION: TPL_OPERATION.name,
    NODE: TPL_NODE.name,
    ARTICLE: TPL_ARTICLE.name,
    STAGE: TPL_STAGE.name,
    REMOTE_MAPPING: TPL_REMOTE_MAPPING.name
  }
}
export const { OPERATION, NODE, ARTICLE, STAGE, REMOTE_MAPPING, DB } = DB_TPL(0)
