export const noop = () => {}
export const isUnDef = (v) => typeof v == 'undefined' || (typeof v == 'number' && isNaN(v)) || v === null
// export const isUnDef = (v) => typeof (v) == 'undefined'
export const isDef = (v) => !!v
// export const isDef = (v) => !!v && (typeof (v) != 'string' || typeof (v) != 'number')
export const isFunc = (v) => typeof v == 'function'
export const isObj = (v) => v != null && typeof v == 'object'
export const isStr = (v) => typeof v == 'string'
export const undef = (old, now) => (isUnDef(now) ? old : now)
export const isDefEqual = (old, now) => (isUnDef(old) ? true : old == now)
export const isEmptyStr = (str) => str.trim() == ''
