export const PM_NULL = new TypeError('PM_NULL')
export const ISPM_NULL = (pm) => PM_NULL === pm

export const accHF = (base, curval) => base + (curval - base) / 2

export const lastindex = (list) => list.length - 1
export const head = (list) => list[0]
export const tail = (list) => list[lastindex(list)]

export const stepval = (v, len) => v / len

export const createLock = () => {
  let bool = false
  return {
    islocked(val) {
      if (val) bool = true
      return bool
    },
    unlock() {
      bool = false
    }
  }
}

export const prev = (arr, id) => {
  console.log({ arr })
  return arr[arr.indexOf(id) - 1]
}
export const next = (arr, id) => {
  return arr[arr.indexOf(id) + 1]
}

// 
export const createLink = (list = [], index = 0) => {
  return {
    add() {},
    del() {},
    next: () => list[index + 1],
    prev: () => list[index - 1],
    head: () => list[0],
    tail: () => list[lastindex]
  }
}

// padding
export const padding = ({ x, y, width, height }, padval) => {
  if (!isObj(padval)) {
    padval = { top: padval, right: padval, bottom: padval, left: padval }
  }
  const { top, right, bottom, left } = padval
  return {
    x: x - left,
    y: y - top,
    width: width + left + right,
    height: height + top + bottom
  }
}

export const round = (v, offset) => {
  if (isDef(offset)) {
    offset = Math.pow(10, offset)
    return Math.round(v * offset) / offset
  }
  return Math.round(v)
}

