import { linecoord } from '../helpers/coord.js'
import theme from '../theme/index.js'

export const updateLink = (path, parentVNode, selfVNode) => {}
export const linkType = (type) => {
  const style = {
    dotted: {
      dasharray: [2, 4]
    }
  }
  return { ...(style[type] || {}) }
}
export const linkFactory =
  (layer) =>
  ({ dots, type }) => {
    console.log({ dots, type })

    const lineTo = (dots) => dots.map(({ x, y }) => `${x} ${y}`).join(' L')
    console.log('line to::', { layer }, lineTo(dots))
    const path = layer
      .path(`M${lineTo(dots)}`)
      .fill('transparent')
      .stroke({ color: theme.color, width: 1, ...linkType(type) })
    // const path = layer.path(`M${x1} ${y1} L400 450 L${x2} ${y2}`).fill('transparent').stroke({ color: theme.color, width: 1, ...linkType(type) })
    const markers = []
    // end arrow icon
    path.marker('end', 20, 6, function (add) {
      add.path('M 0 0 L 10 3 L 0 6 z').fill(theme.path.marker)
      markers.push(add)
    })

    // start circle icon
    if (type != 'circle') {
      const endpoint = 6
      path.marker('start', endpoint, endpoint, function (add) {
        add.circle(endpoint).fill(theme.path.marker)
        add
          .circle(endpoint - 2)
          .dx(1)
          .dy(1)
          .fill('#ccc')
        markers.push(add)
      })
    }

    path.move = (dots) => {
      // console.log({ dots })
      path.attr({ d: `M${lineTo(dots)}` })
      // path.attr({ d: [`M${x1}`, y1, `L${x2}`, y2].join(' ') })
    }

    const remove = path.remove.bind(path)
    path.remove = () => {
      markers.forEach((marker) => marker.remove())
      remove()
    }
    return path
  }
export const solid =
  (layer) =>
  ({ dots }) =>
    linkFactory(layer)({ dots, type: 'solid' })
export const dotted =
  (layer) =>
  ({ dots }) =>
    linkFactory(layer)({ dots, type: 'dotted' })
