// 
export const centerDiffPoint = (viewbox, { x, y }) => {
  const {
    x: vx,
    y: vy,
    window: { width, height, scale, isshowside = false }
  } = viewbox
  // console.log({ width, height }, viewport.sizeTach(isshowside, viewport.detach, width))
  const [sw, sh] = scale(viewport.sizeTach(isshowside, viewport.detach, width), height)
  const [dx, dy] = [x - (vx + sw / 2), y - (vy + sh / 2)]
  return [dx, dy, viewbox]
}

//  [top,right,bottom,left]
export const curViewBoundary = (viewbox) => {
  const {
    x: vx,
    y: vy,
    window: { width, height, scale }
  } = viewbox
  // console.log({ vx, vy, width, height, zoom: scale(1) })
  const [sw, sh] = scale(width, height)
  const [top, right, bottom, left] = [vy, vx + sw, vy + sh, vx]
  return {
    source: [top, right, bottom, left],
    isInViewbox(x, y) {
      return x < left || x > right || y < top || y > bottom
    }
  }
}

export const viewBoundary = () => {
  let [top, right, bottom, left] = [0, 0, 0, 0]
  return {
    add(x, y) {
      if (x < left) return (left = x)
      if (x > right) return (right = x)
      if (y < top) return (top = y)
      if (y > bottom) return (bottom = y)
    },
    boundray() {
      return [top, right, bottom, left]
    }
  }
}
export const vBoundray = viewBoundary()

const equalViewbox = (v1, v2) => v1.width == v2.width && v1.height == v2.height && v1.x == v2.x && v1.y == v2.y
const clearzero = (o) =>
  Object.keys(o)
    .filter((k) => !!o[k])
    .reduce((memo, k) => {
      memo[k] = o[k]
      return memo
    }, {})

