import Snabbdom from 'snabbdom-pragma'
import { multiScale, editIconPath, moreIconPath } from '../node.js'
import { isUnDef, undef, min } from '../../helpers/base.js'
import { multiText } from './text.jsx'

export const rectVNode =
  ({ x, y, width, height, rx = 4, ry = 4, txt, fontsize, fontcolor, bg: fill = '#fff', stroke, isarticle, isstage, ismore = false }) =>
  (sink) => {
    const icon = { width: 16, height: 14 }

    const ratios = [0, -2 / 20]
    const [inner, wrap] = multiScale(min([width, height]), ratios)
    const editiconArg = (width, s) => ({ x: width - s.padding - icon.width, y: s.padding + icon.height })
    const moreiconArg = (width, height, s) => ({ x: width - s.dv + s.padding - 10, y: height - s.dv + s.padding })
    const textSource = multiText(txt, { width, height, color: fontcolor, size: fontsize })
    const stageFlag = (isstage) => {
      if (!isstage) return ''
      return rect({
        x: wrap.padding,
        y: wrap.padding,
        width: width - wrap.dv,
        height: height - wrap.dv,
        fill: 'transparent',
        rx,
        ry,
        stroke: { ...stroke, opacity: 0.3 }
      })
    }
    const articleFlag = (isarticle) => {
      if (!isarticle) return ''
      return <path d={editIconPath(editiconArg(width, inner))} fill="#000000"></path>
    }
    const moreFlag = (ismore) => {
      if (!ismore) return ''
      return <path d={moreIconPath(moreiconArg(width, height, inner))} fill="#000000"></path>
    }

    return (
      <g transform={`translate(${x},${y})`}>
        {stageFlag(isstage)}
        {rect({
          x: inner.padding,
          y: inner.padding,
          width: width - inner.dv,
          height: height - inner.dv,
          fill,
          rx: rx / 2,
          ry: ry / 2,
          stroke
        })}
        {textSource(0, sink)}
        {articleFlag(isarticle)}
        {moreFlag(ismore)}
      </g>
    )
  }

export const rect = ({ x = false, y = false, width, height, fill, rx, ry, stroke, className = false }) => {
  const { width: strokewidth = false, dasharray = false, opacity: strokeopacity = false, color = false } = stroke
  return (
    <rect
      className={className}
      x={x}
      y={y}
      width={width}
      height={height}
      fill={fill}
      rx={rx}
      ry={ry}
      stroke-width={strokewidth}
      stroke-opacity={strokeopacity}
      stroke-dasharray={dasharray}
      stroke={color}
    ></rect>
  )
}
